:root {
  --brand-color: #ffb8ca;
  --color-white: #fff;
  --color-red: #d41443;
  --btn-hover-color-one: #ecebeb;
  --btn-hover-color-two: #c7133e;
}

body {
  margin: 0;
  padding: 0;
  font-family: Titan One, cursive;
}

.hero-header {
  background-color: var(--brand-color);
  background: linear-gradient(-45deg, #ffb8ca, #9f465b, #f6a7bb, #af6678) 0 0 / 400% 400%;
  justify-content: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.hero-header {
  animation: gradient 15s infinite;
}

.mobile-container {
