.js-menu-container {
  display: none;
}

.js-menu-container.is-open {
  z-index: 5;
  width: 250px;
  height: 100%;
  background-color: #e17992;
  flex-shrink: 0;
  padding-top: 20px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.js-close-menu {
  width: 14px;
  height: 14px;
  cursor: pointer;
  background: none;
  border: none;
  flex-shrink: 0;
  padding: 0;
  position: absolute;
  right: 20px;
}

.js-close-menu svg {
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}

.js-close-menu:hover svg {
  scale: 1.5;
}

.mobile-backdrop {
  width: 100%;
}

.buy-now-btn {
  width: 209px;
  height: 44px;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 22px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 60px;
  margin-bottom: 32px;
  margin-left: 20px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.buy-now-btn:hover, .buy-now-btn:focus {
  background-color: var(--btn-hover-color-one);
  transform: translateY(-3px);
}

.buy-now-btn span {
  color: #d41443;
  text-align: center;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
}

.buy-now-vector {
  width: 6px;
  height: 8px;
  flex-shrink: 0;
}

.mobile-navigation-list {
  flex-direction: column;
  gap: 22px;
  padding: 0 20px;
  list-style: none;
  display: flex;
}

.mobile-navigation-item {
  border-bottom: 1px solid #e18298;
  padding-bottom: 12px;
}

.mobile-navigation-item a {
  color: var(--color-white);
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: normal;
  text-decoration: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(-3px);
}

.mobile-navigation-item a:hover {
  color: var(--color-red);
  background-color: var(--color-white);
  border-radius: 10px;
  padding: 10px;
  transform: translateY(-3px);
}

.mobile-navigation-item:first-child a {
  color: var(--color-red);
}

.hero-header-modal-container {
  position: relative;
}

.modal-content-buy {
  height: 600px;
  width: 315px;
  z-index: 20;
  background-color: #ffb8ca;
  border: 3px solid #d41443;
  border-radius: 10px;
  margin: auto;
  font-family: Titan One;
  font-size: 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 4px 8px rgba(255, 22, 131, .357), 0 6px 20px rgba(0, 0, 0, .19);
}

.is-hidden {
  visibility: hidden;
  pointer-events: none;
}

.is-active {
  opacity: 1;
  width: 150%;
  height: 100%;
  z-index: 15;
  background: linear-gradient(-45deg, #29020b 13%, #960528 31%, #eb6988 49%, #f6cfd8 67%) 0 0 / 300% 300%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  animation: modal-gradient 10s infinite;
  position: fixed;
  left: 0;
}

@keyframes modal-gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.title-close-btn-container {
  justify-content: center;
  align-items: flex-start;
  gap: 15px;
  margin-bottom: 130px;
  padding-top: 10px;
  display: flex;
  position: relative;
}

.buy-now-close-btn {
  height: 40px;
  width: 40px;
  cursor: pointer;
  color: var(--color-red);
  background: none;
  border: none;
  font-size: 20px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
